    compareValues(timesheet: any, working_hours: any): boolean {
        const isEqual = Number(timesheet.value) === Number(working_hours);

        const todayCheck = new Date(timesheet.key);
        const isSaturday = todayCheck.getDay() === 6;
        const weekOfMonth = this.getWeekOfMonth(todayCheck);
        const isSecondOrFourthWeek = (weekOfMonth === 2 || weekOfMonth === 4);

        const newDate = new Date();
        const today = startOfDay(new Date(newDate.getFullYear(), newDate.getMonth(), 25));
        const currentWeekMonday = startOfWeek(today, { weekStartsOn: 1 });

        const isWeekdayAndFuture = todayCheck > currentWeekMonday && getDay(todayCheck) >= 1 && getDay(todayCheck) <= 5;

        return isEqual || (isSaturday && isSecondOrFourthWeek) || isWeekdayAndFuture;
    }

    getWeekOfMonth(date: Date): number {
        const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
        const firstDayOfWeek = firstDay.getDay();
        const dayOfMonth = date.getDate();

        return Math.ceil((dayOfMonth + firstDayOfWeek) / 7);
    }
