    changeTimeSheet(){
        this.timesheet_total.total = 0;
        this.weekdays.forEach(day => {
            this.timesheet_total[day].hours = 0;
        });
        this.timesheet_ot_total.total = 0;
        this.weekdays.forEach(day => {
            this.timesheet_ot_total[day].hours = 0;
        });
        this.timesheets.forEach((element, index) => {
            element.total = 0;
            this.weekdays.forEach(day => {
                element[day].hours = this.roundDownToHalf(element[day].hours);
                element.total += element[day].hours;
                this.timesheet_total[day].hours += element[day].hours;
                this.timesheet_total.total += element[day].hours;
            });
        })
        this.timesheet_total_viewtable = JSON.parse(JSON.stringify(this.timesheet_total));
        this.weekdays.forEach(day => {
            if(this.leavesHolidays[day].data[0]){
                if(this.leavesHolidays[day].data[0].duration == "half"){
                    if(this.leavesHolidays[day].data[0].duration_type == 'first_half'){
                        this.timesheet_total_viewtable[day].hours += 4;
                        this.timesheet_total_viewtable.total += 4;
                    } else {
                        this.timesheet_total_viewtable[day].hours += 4.5;
                        this.timesheet_total_viewtable.total += 4.5;
                    }
                } else {
                    this.timesheet_total_viewtable[day].hours += 8.5;
                    this.timesheet_total_viewtable.total += 8.5;
                }
            }
        });
        this.timesheets_ot.forEach(element => {
            element.total = 0;
            this.weekdays.forEach(day => {
                element[day].hours = this.roundDownToHalf(element[day].hours);
                element.total += element[day].hours;
                this.timesheet_ot_total[day].hours += element[day].hours;
                this.timesheet_ot_total.total += element[day].hours;
            });
        })
    }
    roundDownToHalf(value) {
        return Math.max(0, Math.floor(Number(value) * 2) / 2);
    }

    saveTimeSheet(action){
        let checkSubmit = true;
        let timesheetsSave = [];
        let timesheetsOTSave = [];
        this.timesheets.forEach((element, index) => {
            if(!element.task_id){
                let error = false;
                this.weekdays.forEach(day => {
                    if(this.roundDownToHalf(element[day].hours) > 0){
                        error = true;
                    }
                });
                if(error){
                    $('.timesheet_task_'+index).addClass('error-input');
                    checkSubmit = false;
                } else {
                    $('.timesheet_task_'+index).removeClass('error-input');
                }
            } else {
                this.weekdays.forEach(day => {
                    if(element[day].hours > 0){
                        let data = {};
                        data['task_id'] = element.task_id;
                        data['date'] = element[day].date;
                        data['note'] = element[day].note;
                        data['start_time'] = element[day].date + ' 00:00:00'
                        data['end_time'] = this.datePipe.transform(addHours(data['start_time'], element[day].hours), 'yyyy/MM/dd HH:mm:ss');
                        timesheetsSave.push(data);
                        let working_hours = this.working_hours;
                        if(this.leavesHolidays[day].working_hours){
                            working_hours = this.working_hours - this.leavesHolidays[day].working_hours;
                        }
                        if(this.timesheet_total[day].hours != working_hours){
                            checkSubmit = false;
                        }
                    }
                });
                $('.timesheet_task_'+index).removeClass('error-input');
            }
        })
        this.timesheets_ot.forEach((element, index) => {
            if(!element.task_id){
                let error = false;
                this.weekdays.forEach(day => {
                    if(this.roundDownToHalf(element[day].hours) > 0){
                        error = true;
                    }
                });
                if(error){
                    $('.timesheet_ot_task_'+index).addClass('error-input');
                    checkSubmit = false;
                } else {
                    $('.timesheet_ot_task_'+index).removeClass('error-input');
                }
            } else {
                this.weekdays.forEach(day => {
                    if(element[day].hours > 0){
                        let data = {};
                        data['task_id'] = element.task_id;
                        data['date'] = element[day].date;
                        data['note'] = element[day].note;
                        data['start_time'] = element[day].date + ' 00:00:00'
                        data['end_time'] = this.datePipe.transform(addHours(data['start_time'], element[day].hours), 'yyyy/MM/dd HH:mm:ss');
                        timesheetsOTSave.push(data);
                    }
                    if((this.timesheet_total[day].hours < this.working_hours && this.timesheet_ot_total[day].hours > 0) || (this.timesheet_total[day].hours+this.timesheet_ot_total[day].hours) > 24){
                        let isHoliday = false;
                        this.holidays.forEach(element2 => {
                            if(this.datePipe.transform(element2.date, 'yyyy-MM-dd') == this.datePipe.transform(element[day].date, 'yyyy-MM-dd')){
                                isHoliday = true;
                            }
                        });
                        if(day!='sunday' && day !='saturday' && !isHoliday){
                            checkSubmit = false;
                        }
                    }
                });
                $('.timesheet_ot_task_'+index).removeClass('error-input');
            }
        })

        if(this.timesheet_total.total == 0 && this.timesheet_ot_total.total == 0){
            this.toastr.error('Timesheet not entered!');
            return;
        }
        if(checkSubmit && timesheetsSave.length > 0){
            if(action == 'draft'){
                this.timesheetService.saveTimeSheet({data: timesheetsSave, ot:timesheetsOTSave, rangeDate:{start: this.startOfWeek, end:this.endOfWeek}, action:action}).subscribe( data => {
                    this.rerender();
                });
            } else {
                Swal.fire({
                    title: this.translate.instant('common.swal.title'),
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonText: "Yes",
                    cancelButtonText: "Cancel"
                }).then((result) => {
                    if (result.value) {
                        this.timesheetService.saveTimeSheet({data: timesheetsSave, ot:timesheetsOTSave, rangeDate:{start: this.startOfWeek, end:this.endOfWeek}, action:action}).subscribe( data => {
                            this.rerender();
                        });
                    }
                });
            }
        }
    }

