    convertTimeToDecimal(time: any): number {
        if (!time || time.trim() === '') {
            return 0;
        }
        if (typeof time !== 'string') {
          time = String(time);
        }
        let hours: number;
        let minutes: number = 0;
        if (time.includes(':')) {
          [hours, minutes] = time.split(':').map(Number);
        } else {
          hours = Number(time);
        }
        let decimalMinutes = minutes / 60;
        return hours + decimalMinutes;
    }
    isDisabled(data){
        if(this.datePipe.transform(new Date(), 'yyyy/MM/dd HH:mm') >= this.datePipe.transform(addHours(addDays(data.date, this.time_days), this.time_hours), 'yyyy/MM/dd HH:mm') && data.status != 3){
            return true;
        }
        return false;
    }
